<template>
  <div id="product-detail">
    <Header />
    <div class="container">
      <div class="item">
          <div class="image" :style="{backgroundImage: `url(${productByID.url})`}"></div>
          <h1 class="name">{{productByID.name}}</h1>
          <p class="price">{{productByID.price}} USD</p>
          <p class="description">{{productByID.description}}</p>
          <div class="btn">Add to cart</div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Home/Header.vue'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'ProductDetail',
  components: {
    Header
  },
  props: ['id'],
  methods: {
      ...mapActions(['getProductByID'])
  },
  computed: {
    ...mapState(['productByID'])
  },
  created () {
      this.getProductByID(this.id)
  }
}
</script>

<style lang="scss" scoped>
.item {
    padding: 24px 0;
    max-width: 300px;
    margin: 0 auto;
    .image {
        padding-top: 50%;
        background-repeat: no-repeat;
        // background-size: cover;
        background-position: center;
        border-radius: 4px;
    }
    .name {
        margin: 12px 0;
        font-size: 24px;
        line-height: 36px;
    }
    .price {
        color: $color-primary;
        font-size: 16px;
        line-height: 24px;
    }
    .description {
        margin: 12px 0 24px;
        line-height: 24px;
    }
    .btn {
        padding: 12px 24px;
    }
}
</style>